.car-detail_body {
    background-color: rgb(246, 247, 249);
}

.car-detail_body {
    display: flex;
}

.filter-bar_detail {
  width: 330px;
  background-color: palevioletred
}

.car-detail_container {
    height: 1000px;
    max-width: 100%;
    display: flex;
    padding-left: 100px;
}

.car_photos {
    margin-top: 10px;
    padding-right: 30px;
}

.car_banner {
    width: 492px;
    height: 360px;
    left: 32px;
    background: #3563E9;
    border-radius: 10px;
}

.car-upper_ad {
    width: 372px;
    height: 76px;
    font-family: 'Plus Jakarta Sans';
    font-weight: 600;
    font-size: 32px;
    line-height: 120%;
    color: #FFFFFF;
    margin-left: 25px;
    margin-top: 25px;
}

.car-lower_ad {
    width: 284px;
    height: 52px;
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    display: flex;
    align-items: center;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    margin-left: 25px;
    margin-top: 15px;
}









.car_description-detail {
    position: absolute;
    display: block;
    font-weight: 500;
    width: 492px;
    height: 508px;
    right: 30px;
    background: #FFFFFF;
    border-radius: 10px;
    margin-top: 32px;
    
} 

.car_name-detail {
    width: 220px;
    height: 40px;
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 140%;
    display: flex;
    align-items: center;
    color: #1A202C;
    padding-left: 24px;
    padding-top: 10px;
} 

.text_description {
    position: absolute;
    width: 444px;
    height: 120px;
    left: 24px;
    padding-top: 30px;
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 200%;
    letter-spacing: -0.02em;
    color: #3D5278;
}