* {
    box-sizing: border-box;
}

.navbar {
    display: flex;
    justify-content: space-between;
    height: 150px;
    border-bottom: 1px solid rgba(195, 212, 233, 0.4);
}

h1 {
    color: #3563E9;
    font-size: 32px;
    font-weight: 700;
}

.right_nav {
    padding-top: 40px;
    padding-left: 60px;
}

.nav_input {
    box-sizing: border-box;
    display: flex;
    position: absolute;
    width: 492px;
    height: 44px;
    left: 272px;
    top: 40px;
    background: #FFFFFF;
    border: 1px solid rgba(195, 212, 233, 0.4);
    border-radius: 70px;
}

.search_icon {
    float: right;
    padding-left: 210px;
    padding-top: 8px;
}

#nav-input_id {
    padding-left: 30px;
}


.nav_icons {
    display: flex;
}

.nav-icons_link {
    box-sizing: border-box;
    height: 20px;
    width: 18px;
    list-style: none;
    opacity: 0.8;
    margin-left: 30px;
    margin-top: 40px;
    padding: 20px;
   
}


.avatar {
    margin-right: 25px;
    margin-left: 30px;
    height: 23px;
    width: 27px;
}

.car-detail_body {
    background-color: rgb(246, 247, 249);
}

.car-detail_body {
    display: flex;
}

.filter-bar_detail {
  width: 330px;
  background-color: palevioletred
}

.car-detail_container {
    height: 1000px;
    max-width: 100%;
    display: flex;
    padding-left: 100px;
}

.car_photos {
    margin-top: 10px;
    padding-right: 30px;
}

.car_banner {
    width: 492px;
    height: 360px;
    left: 32px;
    background: #3563E9;
    border-radius: 10px;
}

.car-upper_ad {
    width: 372px;
    height: 76px;
    font-family: 'Plus Jakarta Sans';
    font-weight: 600;
    font-size: 32px;
    line-height: 120%;
    color: #FFFFFF;
    margin-left: 25px;
    margin-top: 25px;
}

.car-lower_ad {
    width: 284px;
    height: 52px;
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    display: flex;
    align-items: center;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    margin-left: 25px;
    margin-top: 15px;
}









.car_description-detail {
    position: absolute;
    display: block;
    font-weight: 500;
    width: 492px;
    height: 508px;
    right: 30px;
    background: #FFFFFF;
    border-radius: 10px;
    margin-top: 32px;
    
} 

.car_name-detail {
    width: 220px;
    height: 40px;
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 140%;
    display: flex;
    align-items: center;
    color: #1A202C;
    padding-left: 24px;
    padding-top: 10px;
} 

.text_description {
    position: absolute;
    width: 444px;
    height: 120px;
    left: 24px;
    padding-top: 30px;
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 200%;
    letter-spacing: -0.02em;
    color: #3D5278;
}
